import "./rightbar.css";
import { useContext, useState, useEffect } from "react";
import Online from "../online/Online";
import { AuthContext } from "../../context/AuthContext";
import API from "../../serivce/API";
import { Link } from "react-router-dom";

export default function Rightbar({ user }) {
  const RF = process.env.REACT_APP_PUBLIC_FOLDER;
  const [frineds, setFriends] = useState([]);
  const { user: currentUser } = useContext(AuthContext);
  const [circle, setCircle] = useState([]);

  useEffect(() => {
    const fetchUsers = async () => {
      const res = await API().get(
        `users/friends/${currentUser?._id}`
      );
      setFriends(res.data);
    };
    fetchUsers();
  }, [currentUser]);

  useEffect(() => {
    const fetchCircle = async () => {
      const res = await API().get(
        `users/circle/${user?._id}`
      );
      setCircle(res.data);
    };
    fetchCircle();
  }, [user]);

  const ProfileRightbar = () => {
    return (
      <>
        <h4 className='rightbarTitle'>我的关注</h4>
        <ul className='rightbarFriendList'>
          {frineds.map((u) => (
            <Online key={u._id} user={u} />
          ))}
        </ul>
      </>
    );
  };

  const ProfileRightbarOption = () => {
    return (
      <>
        <h4 className='rightbarTitle'>简介</h4>
        <div className='rightbarInfo'>
          <div className='rightbarInfoItem'>
            <span className='rightbarInfoKey'>居住地:</span>
            <span className='rightbarInfoValue'>
              {user.city}
            </span>
          </div>
          <div className='rightbarInfoItem'>
            <span className='rightbarInfoKey'>家乡:</span>
            <span className='rightbarInfoValue'>
              {user.from}
            </span>
          </div>
          <div className='rightbarInfoItem'>
            <span className='rightbarInfoKey'>职业:</span>
            <span className='rightbarInfoValue'>
              {user.relationship}
            </span>
          </div>
        </div>
        <h4 className='rightbarTitle'>好友圈</h4>
        <div className='rightbarFollowings'>
          {circle.map((u) => {
            return (
              <Link
                to={`/profile/${u.username}`}
                key={u._id}
              >
                <div className='rightbarFollowing'>
                  <img
                    src={
                      user.profilePicture
                        ? RF + user.profilePicture
                        : RF + "person/noAvatar.png"
                    }
                    alt=''
                    className='rightbarFollowingImg'
                  />
                  <span className='rightbarFollowingName'>
                    {u.username}
                  </span>
                </div>
              </Link>
            );
          })}
        </div>
      </>
    );
  };
  return (
    <div className='rightbar'>
      <div className='rightbarWrapper'>
        {user ? (
          <ProfileRightbarOption />
        ) : (
          <ProfileRightbar />
        )}
      </div>
    </div>
  );
}
