import React, {
  useRef,
  useState,
  useContext,
  useEffect,
} from "react";
import {
  PermMedia,
  Label,
  EmojiEmotions,
} from "@material-ui/icons/";

import "./share.css";
import API from "../../serivce/API";
import { AuthContext } from "../../context/AuthContext";

export default function Share({ username }) {
  const desc = useRef("");
  const [file, setFile] = useState(null);
  const { user } = useContext(AuthContext);
  const [hide, setHide] = useState(true);

  const changeVisible = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    setHide(!hide);
  };

  const format = (text) => {
    var replaceRegex = /(\n\r|\r\n|\r|\n)/g;

    text = text || "";

    return text.replace(replaceRegex, "<br/>");
  };

  const submitHandler = async (e) => {
    e.preventDefault();

    const newPost = {
      userId: user._id,
      desc: format(desc.current.value),
    };

    if (file) {
      const data = new FormData();
      const fileName = Date.now() + file.name;
      // 这边append 的顺序不对会导致服务器端娶不到 req.body
      data.append("fileName", fileName);
      data.append("file", file);
      newPost.img = fileName;
      try {
        await API().post("/upload", data);
      } catch (error) {}
    }

    try {
      await API().post("/posts", newPost);
      window.location.reload();
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    const handler = () => {
      setHide(true);
    };
    document.addEventListener("click", handler);

    const submitHandler = async () => {
      const newPost = {
        userId: user._id,
        desc: format(desc.current.value),
      };

      if (file) {
        const data = new FormData();
        const fileName = Date.now() + file.name;
        // 这边append 的顺序不对会导致服务器端娶不到 req.body
        data.append("fileName", fileName);
        data.append("file", file);
        newPost.img = fileName;
        try {
          await API().post("/upload", data);
        } catch (error) {}
      }

      try {
        await API().post("/posts", newPost);
        window.location.reload();
      } catch (error) {
        console.log(error);
      }
    };
    const keydownHandler = (e) => {
      if (e.keyCode === 13 && e.ctrlKey) {
        submitHandler();
      }
    };

    document.addEventListener("keydown", keydownHandler);

    return () => {
      document.removeEventListener(
        "keydown",
        keydownHandler
      );
      document.removeEventListener("click", handler);
    };
  }, [file, user]);

  const ShareComponent = () => (
    <div className='share'>
      <div className='shareWrapper'>
        <div className='shareTop'>
          <img src='' alt='' />
          <textarea
            type='text'
            placeholder='有什么有趣的事情分享给大家?'
            className='shareInput'
            ref={desc}
          />
        </div>
        <hr className='shareHr' />
        {file && (
          <div className='shareImageContainer'>
            <img
              alt=''
              className='shareImg'
              src={URL.createObjectURL(file)}
            ></img>
          </div>
        )}
        <form
          className='shareBottom'
          onSubmit={submitHandler}
        >
          <div className='shareOptions'>
            <div className='shareOption'>
              <label
                htmlFor='file'
                className='shareOptionText'
              >
                <PermMedia
                  htmlColor='tomato'
                  className='shareIcon'
                />

                <input
                  type='file'
                  id='file'
                  style={{ display: "none" }}
                  accept='.png,.jpeg,.jpg'
                  onChange={(e) =>
                    setFile(e.target.files[0])
                  }
                />
              </label>
            </div>
          </div>
          <button className='shareButton' type='submit'>
            发送
          </button>
        </form>
      </div>
    </div>
  );

  // 条件渲染Share 组件
  if (username === undefined) {
    return <ShareComponent />;
  } else if (username === user?.username) {
    return <ShareComponent />;
  } else {
    return null;
  }
}
