import Storage from "../serivce/localStorage";

const AuthReducer = (state, action) => {
  switch (action.type) {
    case "LOGIN_START":
      const startData = {
        user: null,
        isFetching: true,
        error: false,
      };
      Storage.set("authData", startData);
      return startData;

    case "LOGIN_SUCCESS":
      const successData = {
        user: action.payload,
        isFetching: false,
        error: false,
      };
      Storage.set("authData", successData);
      return successData;

    case "LOGIN_FAILURE":
      const failureData = {
        user: null,
        isFetching: false,
        error: action.error,
      };
      Storage.set("authData", failureData);
      return failureData;

    default:
      return state;
  }
};

export default AuthReducer;
