import { MoreVert } from "@material-ui/icons";
import React, { useEffect, useContext } from "react";
import { useState } from "react";
import API from "../../serivce/API";
import { Link } from "react-router-dom";
import { format } from "timeago.js";
import { AuthContext } from "../../context/AuthContext";
import "./post.css";
export default function Post({ post }) {
  const RF = process.env.REACT_APP_PUBLIC_FOLDER;
  const [like, setLike] = useState(post.likes.length);
  const [isLiked, setIsLiked] = useState(false);
  const [visible, setVisible] = useState(false);

  const [user, setUser] = useState({});

  const { user: currentUser } = useContext(AuthContext);

  useEffect(() => {
    const handler = () => {
      setVisible(false);
    };
    document.addEventListener("click", handler);
    const fetchUser = async () => {
      const res = await API().get(
        `/users/?userId=${post.userId}`
      );
      setUser(res.data);
    };
    fetchUser();
    return () => {
      document.removeEventListener("click", handler);
    };
  }, [post.userId, setVisible, visible]);

  const likeHandler = () => {
    setLike(isLiked ? like - 1 : like + 1);
    setIsLiked(!isLiked);
  };

  const handlerClick = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    setVisible(!visible);
  };
  const handlerDelete = () => {
    const deletePost = async () => {
      if (post.userId === currentUser._id) {
        await API().delete("posts/" + post._id, {
          data: {
            userId: currentUser._id,
          },
        });
      } else {
        alert("你不能删除别人的博客");
      }
    };
    deletePost();
    window.location.reload();
  };

  const handlerFollow = () => {
    const followUser = async () => {
      if (currentUser?._id !== post.userId) {
        await API().put(`users/${post.userId}/follow`, {
          userId: currentUser?._id,
        });
        alert("关注成功");
      } else {
        alert("你不能关注自己");
      }
    };
    followUser();
  };

  const handlerUnfolllow = () => {
    const unFollowUser = async () => {
      if (currentUser._id !== post.userId) {
        await API().put(`users/${post.userId}/unfollow`, {
          userId: currentUser._id,
        });
        alert("取消关注成功");
        window.location.reload();
      } else {
        alert("你不能取消关注自己");
      }
    };
    unFollowUser();
  };
  return (
    <div className='post'>
      <div
        className={[
          "postPop",
          visible ? "postPopShow" : "",
        ].join(" ")}
      >
        <div className='selected'>
          <div
            onClick={handlerDelete}
            className='selected__item'
          >
            删除
          </div>
          <div
            className='selected__item'
            onClick={handlerFollow}
          >
            关注
          </div>
          <div className='selected__item'>特别关注</div>
          <div
            className='selected__item'
            onClick={handlerUnfolllow}
          >
            取消关注
          </div>
        </div>
      </div>
      <div className='postWrapper'>
        <div className='postTop'>
          <div className='postTopLeft'>
            <Link to={`/profile/${user.username}`}>
              <img
                className='postProfileImg'
                src={
                  user.profilePicture ||
                  RF + "person/noAvatar.png"
                }
                alt=''
              />
            </Link>
            <span className='postUsername'>
              {user.username}
            </span>
            <span className='postDate'>
              {format(post.createdAt, "zh_CN")}
            </span>
          </div>

          <div className='postTopRight'>
            <MoreVert onClick={handlerClick} />
          </div>
        </div>
        <div className='postCenter'>
          <span
            className='postText'
            dangerouslySetInnerHTML={{ __html: post.desc }}
          ></span>
          <img
            src={RF + post.img}
            alt=''
            className='postImg'
          />
        </div>
        <div className='postBottom'>
          <div className='postBottomLeft'>
            <img
              src='/assets/like.png'
              alt=''
              className='likeIcon'
              onClick={likeHandler}
            />

            <span className='postLikeCounter'>{like}</span>
          </div>
          <div className='postBottomRight'>
            <span className='postCommentText'>
              {post.comment} 评论
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}
